<template>
    <div>
    <h1>Selecciona un Rango Horario</h1>
    <div class="time-slots">
      <div
        v-for="slot in timeSlots"
        :key="slot.id"
        class="time-slot"
      >
        <button @click="toggleTimeSlot(slot.id)">
          {{ slot.label }}
        </button>
      </div>
    </div>
    <p>Rango horario seleccionado: {{ selectedTimeSlot }}</p>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      timeSlots: [
        { id: 1, label: '8:00 - 9:00', selected: false },
        { id: 2, label: '9:00 - 10:00', selected: false },
        { id: 3, label: '10:00 - 11:00', selected: false },
        { id: 4, label: '11:00 - 12:00', selected: false },
        { id: 5, label: '12:00 - 13:00', selected: false },
        { id: 6, label: '13:00 - 14:00', selected: false },
        { id: 7, label: '14:00 - 15:00', selected: false },
        { id: 8, label: '15:00 - 16:00', selected: false },
        { id: 9, label: '16:00 - 17:00', selected: false }
      ],
      selectedTimeSlot: null
    }
  },
  methods: {
    toggleTimeSlot (id) {
      const slot = this.timeSlots.find(slot => slot.id === id)
      slot.selected = !slot.selected
      this.selectedTimeSlot = slot.selected ? slot.label : null
    }
  }
}
</script>



